"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { ShoppingCart, Clock, CheckCircle, AlertTriangle, Plus, ArrowRight, Smartphone, Calendar, MapPin } from "lucide-react"
import { collection, onSnapshot, query, where, orderBy, limit } from "firebase/firestore"
import { db, auth } from "@/firebase/firebaseConfig"
import { useLanguage } from "@/lib/i18n"
import { Skeleton } from "@/components/ui/skeleton"

export default function CustomerDashboard() {
    const { t, language } = useLanguage()
    const router = useRouter()
    const [loading, setLoading] = useState(true)
    const [user, setUser] = useState<any>(null)
    const [stats, setStats] = useState({
        activeRepairs: 0,
        pendingPickup: 0,
        totalOrders: 0,
        completed: 0
    })
    const [recentOrders, setRecentOrders] = useState<any[]>([])

    useEffect(() => {
        const unsub = auth.onAuthStateChanged((u) => {
            setUser(u)
        })
        return () => unsub()
    }, [])

    useEffect(() => {
        if (!user) return

        setLoading(true)

        // Real Firestore subscription
        const customerId = user.uid
        const q = query(
            collection(db, "orders"),
            where("customerId", "==", customerId),
            orderBy("createdAt", "desc"),
            limit(10)
        )

        const unsubscribe = onSnapshot(q, (snapshot) => {
            const orders = snapshot.docs.map((doc) => ({
                id: doc.id,
                ...doc.data()
            }))

            setRecentOrders(orders)

            const active = orders.filter((o: any) => ["in_progress", "on_way"].includes(o.status)).length
            const pending = orders.filter((o: any) => o.status === "pending").length
            const completed = orders.filter((o: any) => o.status === "completed").length

            setStats({
                activeRepairs: active,
                pendingPickup: pending,
                totalOrders: orders.length,
                completed: completed
            })

            setLoading(false)
        })

        return () => unsubscribe()
    }, [user])

    const getStatusColor = (status: string) => {
        switch (status) {
            case "pending": return "bg-yellow-500/20 text-yellow-400 border-yellow-500/30"
            case "in_progress": return "bg-blue-500/20 text-blue-400 border-blue-500/30"
            case "on_way": return "bg-purple-500/20 text-purple-400 border-purple-500/30"
            case "completed": return "bg-green-500/20 text-green-400 border-green-500/30"
            case "cancelled": return "bg-red-500/20 text-red-400 border-red-500/30"
            default: return "bg-gray-500/20 text-gray-400 border-gray-500/30"
        }
    }

    const getStatusLabel = (status: string) => {
        const labels: Record<string, string> = {
            pending: t("Pending"),
            in_progress: t("In Progress"),
            on_way: t("On the Way"),
            completed: t("Completed"),
            cancelled: t("Cancelled")
        }
        return labels[status] || status
    }

    return (
        <div className="space-y-8 animate-in fade-in duration-500">
            {/* Header */}
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
                <div>
                    <h1 className="text-3xl font-bold tracking-tight text-white">{t("My Dashboard")}</h1>
                    <p className="text-white/60 mt-1">{t("Track your device repairs and order history")}</p>
                </div>
                <Button
                    onClick={() => router.push("/book")}
                    className="bg-gradient-to-r from-cyan-500 to-blue-600 hover:from-cyan-400 hover:to-blue-500 text-white font-semibold shadow-lg shadow-cyan-500/25"
                >
                    <Plus className="w-4 h-4 mr-2" />
                    {t("Book New Repair")}
                </Button>
            </div>

            {/* Stats Grid */}
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
                <Card className="relative overflow-hidden bg-gradient-to-br from-cyan-500/10 via-blue-500/10 to-purple-500/10 border-white/10 backdrop-blur-md">
                    <div className="pointer-events-none absolute inset-0">
                        <div className="absolute -top-20 -right-24 w-72 h-72 rounded-full bg-cyan-500/10 blur-2xl" />
                    </div>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium text-white/80">{t("Active Repairs")}</CardTitle>
                        <Clock className="h-5 w-5 text-cyan-400" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-3xl font-bold text-white">{loading ? "..." : stats.activeRepairs}</div>
                        <p className="text-xs text-white/60 mt-1">{t("Devices being repaired")}</p>
                    </CardContent>
                </Card>

                <Card className="bg-white/5 border-white/10 backdrop-blur-sm">
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium text-white/80">{t("Pending Orders")}</CardTitle>
                        <AlertTriangle className="h-5 w-5 text-yellow-400" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-3xl font-bold text-white">{loading ? "..." : stats.pendingPickup}</div>
                        <p className="text-xs text-white/60 mt-1">{t("Awaiting technician")}</p>
                    </CardContent>
                </Card>

                <Card className="bg-white/5 border-white/10 backdrop-blur-sm">
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium text-white/80">{t("Completed")}</CardTitle>
                        <CheckCircle className="h-5 w-5 text-green-400" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-3xl font-bold text-white">{loading ? "..." : stats.completed}</div>
                        <p className="text-xs text-white/60 mt-1">{t("Successfully repaired")}</p>
                    </CardContent>
                </Card>

                <Card className="bg-white/5 border-white/10 backdrop-blur-sm">
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium text-white/80">{t("Total Orders")}</CardTitle>
                        <ShoppingCart className="h-5 w-5 text-purple-400" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-3xl font-bold text-white">{loading ? "..." : stats.totalOrders}</div>
                        <p className="text-xs text-white/60 mt-1">{t("All time repairs")}</p>
                    </CardContent>
                </Card>
            </div>

            {/* Recent Orders */}
            <Card className="bg-white/5 border-white/10 backdrop-blur-sm">
                <CardHeader className="flex flex-row items-center justify-between">
                    <CardTitle className="text-white">{t("Recent Orders")}</CardTitle>
                    <Button
                        variant="ghost"
                        onClick={() => router.push("/customer/orders")}
                        className="text-cyan-400 hover:text-cyan-300 hover:bg-cyan-500/10"
                    >
                        {t("View All")}
                        <ArrowRight className="w-4 h-4 ml-2" />
                    </Button>
                </CardHeader>
                <CardContent>
                    {loading ? (
                        <div className="space-y-4">
                            {[...Array(3)].map((_, i) => (
                                <Skeleton key={i} className="h-24 w-full bg-white/10 rounded-xl" />
                            ))}
                        </div>
                    ) : recentOrders.length === 0 ? (
                        <div className="text-center py-12">
                            <Smartphone className="w-12 h-12 text-white/20 mx-auto mb-4" />
                            <p className="text-white/50">{t("No orders yet")}</p>
                            <Button
                                onClick={() => router.push("/book")}
                                className="mt-4 bg-cyan-500 hover:bg-cyan-400 text-black font-medium"
                            >
                                {t("Book Your First Repair")}
                            </Button>
                        </div>
                    ) : (
                        <div className="space-y-4">
                            {recentOrders.slice(0, 5).map((order) => (
                                <div
                                    key={order.id}
                                    className="flex flex-col md:flex-row items-start md:items-center justify-between p-4 rounded-xl bg-white/5 border border-white/5 hover:bg-white/10 transition-all cursor-pointer"
                                    onClick={() => router.push(`/customer/orders/${order.id}`)}
                                >
                                    <div className="flex gap-4 items-start">
                                        <div className="h-12 w-12 rounded-xl bg-gradient-to-br from-cyan-500/20 to-blue-500/20 flex items-center justify-center shrink-0">
                                            <Smartphone className="h-6 w-6 text-cyan-400" />
                                        </div>
                                        <div>
                                            <h4 className="font-semibold text-white">
                                                {order.device}
                                                <span className="text-white/40 text-sm ml-2">#{order.id}</span>
                                            </h4>
                                            <p className="text-sm text-white/60">{order.issue}</p>
                                            <div className="flex flex-wrap gap-3 mt-2 text-xs text-white/50">
                                                <span className="flex items-center gap-1">
                                                    <Calendar className="w-3 h-3" />
                                                    {new Date(order.date || order.createdAt?.toDate?.()).toLocaleDateString()}
                                                </span>
                                                {order.technicianName && (
                                                    <span className="text-cyan-400/80">
                                                        {t("Technician")}: {order.technicianName}
                                                    </span>
                                                )}
                                            </div>
                                        </div>
                                    </div>

                                    <div className="mt-4 md:mt-0 flex flex-col items-end gap-2">
                                        <Badge variant="outline" className={getStatusColor(order.status)}>
                                            {getStatusLabel(order.status)}
                                        </Badge>
                                        <span className="text-cyan-400 font-mono font-medium">{order.price} AED</span>
                                    </div>
                                </div>
                            ))}
                        </div>
                    )}
                </CardContent>
            </Card>

            {/* Quick Actions */}
            <div className="grid gap-4 md:grid-cols-3">
                <Card
                    className="bg-gradient-to-br from-cyan-500/10 to-blue-600/10 border-white/10 hover:border-cyan-500/30 transition-all cursor-pointer group"
                    onClick={() => router.push("/book")}
                >
                    <CardContent className="flex items-center gap-4 p-6">
                        <div className="h-12 w-12 rounded-xl bg-cyan-500/20 flex items-center justify-center group-hover:scale-110 transition-transform">
                            <Plus className="h-6 w-6 text-cyan-400" />
                        </div>
                        <div>
                            <h3 className="font-semibold text-white">{t("Book Repair")}</h3>
                            <p className="text-sm text-white/60">{t("Schedule a new repair")}</p>
                        </div>
                    </CardContent>
                </Card>

                <Card
                    className="bg-white/5 border-white/10 hover:border-cyan-500/30 transition-all cursor-pointer group"
                    onClick={() => router.push("/customer/track")}
                >
                    <CardContent className="flex items-center gap-4 p-6">
                        <div className="h-12 w-12 rounded-xl bg-purple-500/20 flex items-center justify-center group-hover:scale-110 transition-transform">
                            <MapPin className="h-6 w-6 text-purple-400" />
                        </div>
                        <div>
                            <h3 className="font-semibold text-white">{t("Track Repair")}</h3>
                            <p className="text-sm text-white/60">{t("Check repair status")}</p>
                        </div>
                    </CardContent>
                </Card>

                <Card
                    className="bg-white/5 border-white/10 hover:border-cyan-500/30 transition-all cursor-pointer group"
                    onClick={() => router.push("/contact")}
                >
                    <CardContent className="flex items-center gap-4 p-6">
                        <div className="h-12 w-12 rounded-xl bg-green-500/20 flex items-center justify-center group-hover:scale-110 transition-transform">
                            <ShoppingCart className="h-6 w-6 text-green-400" />
                        </div>
                        <div>
                            <h3 className="font-semibold text-white">{t("Contact Support")}</h3>
                            <p className="text-sm text-white/60">{t("Get help with orders")}</p>
                        </div>
                    </CardContent>
                </Card>
            </div>
        </div>
    )
}
